//================MAIN PAGE styles ============
.header-logo {
  display: flex;
  align-items: center;
  align-items: center;
  @media screen and (min-width: 768px) {
    // padding: 40px 32px;
  }
  &__link {
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 40px 0 54px 20px;
    fill: $main-title-color;
  }

  &__text {
    display: none;
  }
  @media screen and (min-width: 768px) {
    .header-logo__text {
      display: inline-block;
      margin-left: 10px;
      font-style: normal;
      font-weight: 500;
      font-size: 30px;
      line-height: calc(35 / 30);
      color: $main-title-color;
    }
  }
}

.header-nav {
  margin-left: auto;
}

.header-list {
  display: flex;
  list-style: none;
  padding: 0;
  align-items: center;
  &__item:not(:last-child) {
    margin-right: 39px;
  }

  &__link {
    display: flex;
    text-align: center;
    padding: 44px 0 60px 0;
    // border: none;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: calc(14 / 12);
    text-transform: uppercase;
    color: $main-title-color;
    background-color: transparent;
    cursor: pointer;

    @media screen and (min-width: 768px) {
      padding: 49px 0 52px 0;
    }
  }
}

.current-page {
  position: relative;
  &::after {
    content: '';
    display: inline-block;
    position: absolute;
    bottom: -8px;
    left: 0;
    width: 100%;
    background-color: $warning-color;
    border: 2px solid $warning-color;
  }
}

// ================FORM styles ===============================

.header-form {
  position: relative;
  display: flex;
  justify-content: center;
  padding-bottom: 92px;
  padding-left: 20px;
  padding-right: 20px;
  //   width: 280px;
  @media screen and (min-width: 768px) {
    padding-bottom: 80px;
  }

  &__input-wrap {
    position: relative;
    // border-bottom: 0.5px solid #ffffff;

    align-items: center;
    height: 22px;
    width: 280px;
    border-bottom: 0.5px solid $main-title-color;
    @media screen and (min-width: 768px) {
      width: 336px;
    }
    @media screen and (min-width: 1200px) {
      width: 394px;
    }
  }

  &__input {
    border: none;
    outline: none;
    background-color: transparent;
    width: 100%;
    color: $main-title-color;
  }

  &__input::placeholder {
    color: $main-title-color;
  }

  &__button {
    background-color: transparent;
    border: none;
    width: 12px;
    height: 12px;
  }

  &__search-icon {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 2px;
    width: 15px;
    height: 15px;
    fill: $main-title-color;
  }
}

// ================ LIBRARY styles==================
.header-library {
  &__list {
    display: flex;
    justify-content: center;
    padding-bottom: 65px;
  }

  &__item:not(:last-child) {
    margin-right: 20px;
    @media screen and (min-width: 768px) {
      margin-right: 32px;
    }
    @media screen and (min-width: 1280px) {
      margin-right: 16px;
    }
  }

  &__btn {
    width: 130px;
    height: 44px;
    border: 1px solid $main-title-color;
    border-radius: 5px;
    background-color: transparent;
    color: $main-title-color;
    box-shadow: (0px 8px 43px rgba(255, 107, 1, 0.6));

    @media screen and (min-width: 768px) {
      width: 152px;
    }
    @media screen and (min-width: 1280px) {
      width: 148px;
      height: 42px;
    }
  }
  &__btn:hover {
    background-color: $main-accent-color;
  }
  &__btn:focus {
    background-color: $main-accent-color;
  }
}

// // // ============ Стили background ========================

.page-header-bcg {
  //background-color: $header-background-color;
  background-image: linear-gradient(
      $header-background-color,
      $header-background-color
    ),
    url(../images/header/header_mobile_1s_@1x.jpg);

  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: linear-gradient(
        $header-background-color,
        $header-background-color
